import React from "react";
import PropTypes from "prop-types";

import AdkomstVinter from "./AccessWinter";
import AdkomstSommer from "./AccessSummer";

const Access = props => {
  const { theme, info } = props;
  const winter = info && info.private && info.private.winter;
  const summer = info && info.private && info.private.winter;

  if (!info) {
    return null;
  }

  return (
    <React.Fragment>
      <AdkomstSommer html={summer} theme={theme} />
      <AdkomstVinter html={winter} theme={theme} />
    </React.Fragment>
  );
};

Access.propTypes = {
  info: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default Access;



