import React from "react";
import PropTypes from "prop-types";

const ContactInfo = props => {
  const {
    info: { name, address1, address2, email, postNumber, postPlace, telephone, type } = {}
  } = props;

  return (
    <React.Fragment>
      {type && <h3>{type}</h3>}
      {name && (
        <React.Fragment>
          Navn: <b>{name}</b>
          <br />
        </React.Fragment>
      )}
      {email && (
        <React.Fragment>
          Epost:{" "}
          <b>
            <a href={"mailto:" + email}>{email}</a>
          </b>
          <br />
        </React.Fragment>
      )}
      {telephone && (
        <React.Fragment>
          Telefon:{" "}
          <b>
            <a href={"tel:" + telephone}>{telephone}</a>
          </b>
          <br />
        </React.Fragment>
      )}
      {address1 && (
        <React.Fragment>
          Adresse: <br />
          <b>
            {address1}
            <br />
            {address2}
            <br />
            {postNumber} {postPlace}
          </b>
        </React.Fragment>
      )}
    </React.Fragment>
  );
};

ContactInfo.propTypes = {
  info: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default ContactInfo;



