import React from "react";
import PropTypes from "prop-types";
import Img from "gatsby-image";
import PhotoCaption from "./PhotoCaption";

const Photo = props => {
  const { description, fotograph, theme } = props;

  return (
    <React.Fragment>
      <Img {...props} class="photo" />
      <PhotoCaption description={description} fotograph={fotograph} theme={theme} />
    </React.Fragment>
  );
};

Photo.propTypes = {
  description: PropTypes.string,
  fotograph: PropTypes.shape({}),
  theme: PropTypes.object.isRequired
};

export default Photo;



