import React from "react";
import PropTypes from "prop-types";

const PaperMaps = props => {
  const { maps, theme } = props;

  if (!maps) {
    return null;
  }

  return (
    <React.Fragment>
      <div className="paperMap">
        <h2>Papir kart</h2>
        <ul>{maps.map(text => <li key={text}>{text}</li>)}</ul>
      </div>
      <style jsx>{`
        .paperMap {
          animation-name: entry;
          animation-duration: ${theme.time.duration.long};

          :global(h2) {
            margin: 1.5em 0 1em;
            line-height: ${theme.font.lineHeight.s};
            font-size: ${theme.font.size.l};
          }

          :global(ul) {
            list-style: circle;
            margin: 0 0 1.5em;
            padding: 0 0 0 1.5em;
          }
          :global(li) {
            margin: 0.7em 0;
            line-height: 1.5;
          }
        }

        @keyframes entry {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
      `}</style>
    </React.Fragment>
  );
};

PaperMaps.propTypes = {
  maps: PropTypes.array,
  theme: PropTypes.object.isRequired
};

export default PaperMaps;



