import React from "react";
import PropTypes from "prop-types";

const Tags = props => {
  const { tags, theme } = props;

  if (!Array.isArray(tags) || tags.length === 0) {
    return null;
  }

  return (
    <React.Fragment>
      <div className="paperMap">
        <h2>Fasiliteter</h2>
        <ul>{tags.map(tag => <li key={tag}>{tag}</li>)}</ul>
      </div>
      <style jsx>{`
        .paperMap {
          animation-name: entry;
          animation-duration: ${theme.time.duration.long};

          :global(h2) {
            margin: 1.5em 0 1em;
            line-height: ${theme.font.lineHeight.s};
            font-size: ${theme.font.size.l};
          }

          :global(ul) {
            list-style: circle;
            margin: 0 0 1.5em;
            padding: 0 0 0 1.5em;
          }
          :global(li) {
            margin: 0.7em 0;
            line-height: 1.5;
          }
        }

        @keyframes entry {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
      `}</style>
    </React.Fragment>
  );
};

Tags.propTypes = {
  tags: PropTypes.array.isRequired,
  theme: PropTypes.object.isRequired
};

export default Tags;



