import React from "react";
import PropTypes from "prop-types";

const Licence = props => {
  const { text } = props;

  if (!text) {
    return null;
  }

  return <React.Fragment>{text}</React.Fragment>;
};

Licence.propTypes = {
  text: PropTypes.string.isRequired
};

export default Licence;



