import React from "react";
import PropTypes from "prop-types";

import Seo from "../components/Seo";
import Article from "../components/Article";
import Place from "../components/Place";
import { ThemeContext } from "../layouts";

const PlaceTemplate = props => {
  const {
    data: {
      page,
      site: {
        siteMetadata: { facebook }
      }
    }
  } = props;

  return (
    <React.Fragment>
      <ThemeContext.Consumer>
        {theme => (
          <Article theme={theme}>
            <Place page={page} theme={theme} />
          </Article>
        )}
      </ThemeContext.Consumer>

      <Seo data={{ frontmatter: { title: page.title } }} facebook={facebook} />
    </React.Fragment>
  );
};

PlaceTemplate.propTypes = {
  data: PropTypes.object.isRequired
};

export default PlaceTemplate;

//eslint-disable-next-line no-undef
export const pageQuery = graphql`
  query PlaceByPath($id: String!) {
    page: placesJson(id: { eq: $id }) {
      id
      name
      description
      licence
      paperMaps
      municipality
      access {
        private {
          summer
          winter
        }
      }
      tags
      images: childLocalImage {
        localImageFile {
          childImageSharp {
            sizes(maxWidth: 1240) {
              ...GatsbyImageSharpSizes
            }
          }
        }
      }
      photos {
        id
        description
        url
        fotograph {
          name
          email
        }
      }
    }
    site {
      siteMetadata {
        facebook {
          appId
        }
      }
    }
  }
`;



