import React from "react";
import PropTypes from "prop-types";

import Headline from "../Article/Headline";
import Bodytext from "../Article/Bodytext";

import Access from "./Access";
import Licence from "./Licence";
import PaperMaps from "./PaperMaps";
import Tags from "./Tags";
import Contact from "./Contact";
import Photo from "./Photo";
import PhotoGallery from "./PhotoGallery";

const Place = props => {
  const {
    page: {
      description,
      name,
      access,
      licence,
      paperMaps,
      contactInfo,
      tags,
      images: { localImageFile },
      photos
    },
    theme
  } = props;
  const images = localImageFile.map((image, index) => ({
    ...image.childImageSharp,
    ...photos[index]
  }));

  const mainImage = images.find(() => true);
  const gallery = images.slice(1);
  console.log(images, photos);
  return (
    <React.Fragment>
      <header>
        <Headline title={name} theme={theme} />
      </header>
      <Photo {...mainImage} theme={theme} />
      <Bodytext html={description} theme={theme} />
      <Access info={access} theme={theme} />
      <PaperMaps maps={paperMaps} theme={theme} />
      <Contact list={contactInfo} theme={theme} />
      <Tags tags={tags} theme={theme} />
      <PhotoGallery photos={gallery} theme={theme} />
      <Licence text={licence} />
    </React.Fragment>
  );
};

Place.propTypes = {
  page: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default Place;



